<template>
  <div class="musicPlayer" v-if="theUrl">
    <!-- ËøôÈáåËÆæÁΩÆ‰∫ÜrefÂ±ûÊÄßÂêéÔºåÂú®vueÁªÑ‰ª∂‰∏≠ÔºåÂ∞±ÂèØ‰ª•Áî®this.$refs.audioÊù•ËÆøÈóÆËØ•domÂÖÉÁ¥† -->
    <audio
      ref="audio"
      class="dn"
      :src="url"
      :preload="audio.preload"
      @play="onPlay"
      @error="onError"
      @waiting="onWaiting"
      @pause="onPause"
      @timeupdate="onTimeupdate"
      @loadedmetadata="onLoadedmetadata"
    ></audio>
    <div>
      <!--      <el-button type="text" @click="startPlayOrPause"-->
      <!--        >{{ audio.playing | transPlayPause }}-->
      <!--      </el-button>-->
      <el-button
        class="play-button"
        type="text"
        @click="startPlayOrPause"
        v-if="audio.playing"
        ><img src="../assets/toolLogo/ÊöÇÂÅú1.png" style="width: 25px" />
      </el-button>
      <el-button
        class="play-button"
        type="text"
        @click="startPlayOrPause"
        v-if="!audio.playing"
        ><img src="../assets/toolLogo/Êí≠Êîæ2.png" style="width: 25px" />
      </el-button>
      <!--      <el-button-->
      <!--        v-show="!controlList.noSpeed"-->
      <!--        type="text"-->
      <!--        @click="changeSpeed"-->
      <!--        >{{ audio.speed | transSpeed }}</el-button>-->

      <el-tag class="currentTime" type="info">{{
        audio.currentTime | formatSecond
      }}</el-tag>

      <el-slider
        v-show="!controlList.noProcess"
        v-model="sliderTime"
        :format-tooltip="formatProcessToolTip"
        @change="changeCurrentTime"
        class="slider"
      ></el-slider>

      <el-tag class="currentTime" type="info">{{
        audio.maxTime | formatSecond
      }}</el-tag>

      <!--      <el-button-->
      <!--        v-show="!controlList.noMuted"-->
      <!--        type="text"-->
      <!--        @click="startMutedOrNot"-->
      <!--        >{{ audio.muted | transMutedOrNot }}</el-button-->
      <!--      >-->
      <el-button
        class="vol-button"
        v-show="!controlList.noMuted"
        type="text"
        @click="startMutedOrNot"
        v-if="audio.muted"
        ><img src="../assets/toolLogo/Èü≥Èáè2.png" style="width: 25px"
      /></el-button>
      <el-button
        v-show="!controlList.noMuted"
        class="vol-button"
        type="text"
        v-if="!audio.muted"
        @click="startMutedOrNot"
        ><img src="../assets/toolLogo/ÈùôÈü≥2.png" style="width: 25px"
      /></el-button>

      <el-slider
        v-show="!controlList.noVolume"
        v-model="volume"
        :format-tooltip="formatVolumeToolTip"
        @change="changeVolume"
        class="slider"
      ></el-slider>

      <a
        :href="url"
        v-show="!controlList.noDownload"
        target="_blank"
        class="download"
        download
        ><img src="../assets/toolLogo/‰∏ãËΩΩ2.png" style="width: 30px"
      /></a>
    </div>
  </div>
</template>

<script>
function realFormatSecond(second) {
  var secondType = typeof second;
  if (secondType === "number" || secondType === "string") {
    second = parseInt(second);
    var hours = Math.floor(second / 3600);
    second = second - hours * 3600;
    var mimute = Math.floor(second / 60);
    second = second - mimute * 60;
    return (
      hours + ":" + ("0" + mimute).slice(-2) + ":" + ("0" + second).slice(-2)
    );
  } else {
    return "0:00:00";
  }
}
export default {
  props: {
    theUrl: {
      type: String,
      required: true
    },
    theSpeeds: {
      type: Array,
      default() {
        return [1, 1.5, 2];
      }
    },
    theControlList: {
      type: String,
      default: ""
    }
  },
  name: "VueAudio",
  data() {
    return {
      url: "",
      audio: {
        currentTime: 0,
        maxTime: 0,
        playing: false,
        muted: false,
        speed: 1,
        // waiting: true,
        preload: "auto"
      },
      sliderTime: 0,
      volume: 100,
      speeds: this.theSpeeds,
      controlList: {
        // ‰∏çÊòæÁ§∫‰∏ãËΩΩ
        noDownload: false,
        // ‰∏çÊòæÁ§∫ÈùôÈü≥
        noMuted: false,
        // ‰∏çÊòæÁ§∫Èü≥ÈáèÊù°
        noVolume: false,
        // ‰∏çÊòæÁ§∫ËøõÂ∫¶Êù°
        noProcess: false,
        // Âè™ËÉΩÊí≠Êîæ‰∏Ä‰∏™
        onlyOnePlaying: false,
        // ‰∏çË¶ÅÂø´ËøõÊåâÈíÆ
        noSpeed: false
      }
    };
  },
  watch: {
    theUrl(value) {
      console.log(value);
      this.url = value;
    }
  },
  methods: {
    setControlList() {
      let controlList = this.theControlList.split(" ");
      controlList.forEach(item => {
        if (this.controlList[item] !== undefined) {
          this.controlList[item] = true;
        }
      });
    },
    changeSpeed() {
      let index = this.speeds.indexOf(this.audio.speed) + 1;
      this.audio.speed = this.speeds[index % this.speeds.length];
      this.$refs.audio.playbackRate = this.audio.speed;
    },
    startMutedOrNot() {
      this.$refs.audio.muted = !this.$refs.audio.muted;
      this.audio.muted = this.$refs.audio.muted;
    },
    // Èü≥ÈáèÊù°toolTip
    formatVolumeToolTip(index) {
      return "Èü≥ÈáèÊù°: " + index;
    },
    // ËøõÂ∫¶Êù°toolTip
    formatProcessToolTip(index = 0) {
      index = parseInt((this.audio.maxTime / 100) * index);
      return "ËøõÂ∫¶Êù°: " + realFormatSecond(index);
    },
    // Èü≥ÈáèÊîπÂèò
    changeVolume(index = 0) {
      this.$refs.audio.volume = index / 100;
      this.volume = index;
    },
    // Êí≠ÊîæË∑≥ËΩ¨
    changeCurrentTime(index) {
      this.$refs.audio.currentTime = parseInt(
        (index / 100) * this.audio.maxTime
      );
    },
    startPlayOrPause() {
      return this.audio.playing ? this.pausePlay() : this.startPlay();
    },
    // ÂºÄÂßãÊí≠Êîæ
    startPlay() {
      this.$refs.audio.play();
    },
    // ÊöÇÂÅú
    pausePlay() {
      this.$refs.audio.pause();
    },
    // ÂΩìÈü≥È¢ëÊöÇÂÅú
    onPause() {
      this.audio.playing = false;
    },
    // ÂΩìÂèëÁîüÈîôËØØ, Â∞±Âá∫Áé∞loadingÁä∂ÊÄÅ
    onError() {
      this.audio.waiting = true;
    },
    // ÂΩìÈü≥È¢ëÂºÄÂßãÁ≠âÂæÖ
    onWaiting(res) {
      console.log(res);
    },
    // ÂΩìÈü≥È¢ëÂºÄÂßãÊí≠Êîæ
    onPlay(res) {
      console.log(res);
      this.audio.playing = true;
      this.audio.loading = false;
      if (!this.controlList.onlyOnePlaying) {
        return;
      }
      let target = res.target;
      let audios = document.getElementsByTagName("audio");
      [...audios].forEach(item => {
        if (item !== target) {
          item.pause();
        }
      });
    },
    // ÂΩìtimeupdate‰∫ã‰ª∂Â§ßÊ¶ÇÊØèÁßí‰∏ÄÊ¨°ÔºåÁî®Êù•Êõ¥Êñ∞Èü≥È¢ëÊµÅÁöÑÂΩìÂâçÊí≠ÊîæÊó∂Èó¥
    onTimeupdate(res) {
      // console.log('timeupdate')
      // console.log(res)
      this.audio.currentTime = res.target.currentTime;
      this.sliderTime = parseInt(
        (this.audio.currentTime / this.audio.maxTime) * 100
      );
    },
    // ÂΩìÂä†ËΩΩËØ≠Èü≥ÊµÅÂÖÉÊï∞ÊçÆÂÆåÊàêÂêéÔºå‰ºöËß¶ÂèëËØ•‰∫ã‰ª∂ÁöÑÂõûË∞ÉÂáΩÊï∞
    // ËØ≠Èü≥ÂÖÉÊï∞ÊçÆ‰∏ªË¶ÅÊòØËØ≠Èü≥ÁöÑÈïøÂ∫¶‰πãÁ±ªÁöÑÊï∞ÊçÆ
    onLoadedmetadata(res) {
      console.log("loadedmetadata");
      console.log(res);
      // this.audio.waiting = false;
      this.audio.maxTime = parseInt(res.target.duration);
      this.startPlay();
    }
  },
  filters: {
    formatSecond(second = 0) {
      return realFormatSecond(second);
    },
    transPlayPause(value) {
      return value ? "ÊöÇÂÅú" : "Êí≠Êîæ";
    },
    transMutedOrNot(value) {
      return value ? "ÊîæÈü≥" : "ÈùôÈü≥";
    },
    transSpeed(value) {
      return "Âø´Ëøõ: x" + value;
    }
  },
  created() {
    this.setControlList();
  }
};
</script>

